@import "./../../mixins";
@import "./../../mobileVariable";
.backgroundPhoto--installation {
  position: relative;
  height: 100vh;
  &:after {
    width: 100%;
    height: 100%;
    content: "";
    display: flex;
    position: absolute;
    justify-content: space-between;
    background-color: #f3f3f3;
    background-image: url("../../assets/paneleMonter.jpg");
    background-size: cover;
    background-position: top;
    filter: brightness(0.9);
  }

  .installation__text {
    p {
      width: 90%;
      color: white;
      font-size: 2rem;
      justify-content: center;
      align-items: center;
    }
  }
}
@media #{$mobile} {
  .backgroundPhoto--installation {
    position: relative;
    justify-content: flex-start;

    &:after {
      width: 100%;
      height: 100%;
      content: "";
      display: flex;
      position: absolute;
      justify-content: space-between;
      background-color: #f3f3f3;
      background-image: url("../../assets/paneleMonter.jpg");
      background-size: cover;
      background-position: center;
      filter: brightness(0.9);
    }

    .installation__text {
      margin-top: 2rem;
      p {
        width: 90%;
        color: white;
        font-size: 0.9rem;
        text-align: center;
      }
    }
  }
}
